.card-body {
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0px 5px 20px rgba(0, 55, 128, 0.2);
}

.section-body {
    //border-bottom: 1px solid @border-color-base;
}

.btn-sm {
    padding: 5px 10px;
    height: 26px;
    line-height: normal;
    font-size: 14px;
    border-radius: 5px;
    font-weight: normal;
}

.ss-app-btn-sm {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    padding: 5px 10px;
    border-radius: 5px;
    font-weight: normal;
}

// overwrite
.ss-app-picker-cell {
    .ss-app-picker-cell-inner {
        border-radius: 0;
    }
}

.ss-app-picker-cell-in-view.ss-app-picker-cell-today .ss-app-picker-cell-inner::before {
    border-radius: 0;
}