.ss-app-payslip-overview,
.ss-app-pay-details {
	@media screen and (min-width: @screen-xxl) {
		width: calc(100% - 375px);
		padding-right: 48px;
	}

	@media screen and (min-width: @screen-xl) and (max-width: @screen-xl-max) {
		width: calc(100% - 330px);
		padding-right: 32px;
	}
}

.ss-app-payslip-overview-field {
	padding: 24px;
	text-align: center;
	margin-bottom: 28px;
	min-height: 125px;

	> span {
		display: block;
	}

	@media screen and (max-width: @screen-sm-max) {
		margin-bottom: 20px;
		min-height: 119px;
	}

	@media screen and (max-width: @screen-sm-min) {
		min-height: 108px;
	}
}

.ss-app-overview-label {
	font-size: 18px;
	line-height: 1.6667em;
	@media screen and (min-width: @screen-md) and (max-width: @screen-lg-max) {
		font-size: 15px;
	}
	@media screen and (max-width: @screen-sm-max) {
		font-size: 14px;
	}
}

.ss-app-overview-value {
	font-family: "Montserrat", sans-serif;
	font-size: 24px;
	font-weight: 700;
	line-height: 1.2em;
	@media screen and (min-width: @screen-md) and (max-width: @screen-lg-max) {
		font-size: 20px;
	}
	@media screen and (max-width: @screen-sm-max) {
		font-size: 18px;
	}
}

.ss-app-overview-extra {
	font-size: 14px;
	line-height: 1.3em;
	@media screen and (max-width: @screen-sm-max) {
		font-size: 12px;
	}
}
