@import '../styles/variables.less';

.ss-app-body {
    padding: 5px;
}

.leave-nav-bar {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    //height: 25px;
}

.leave-nav-bar-buttons {
    position: relative;
    border-radius: 30px;
    background: @blue;
    overflow: hidden;
    //border-top-left-radius: 30px;

    span {
        display: inline-block;
        color: white;
        padding: 10px;
        background: @blue;
        cursor: pointer;

        &:hover {
            color: white;
            background: @blue2;
        }

        &:first-child {
            padding-left: 20px;
        }

        &:last-child {
            padding-right: 20px;
        }
    }

}

.help-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    margin-left: 5px;
    color: white;
    border-radius: 50%;
    background: @blue;
}

.emp-card {
    max-width: 40%;
    min-width: 40%;
    text-align: center;

}

.team-leave-input-width {
    width: 120px;
}

.leave-history {
    .intro-label {
        display: inline-block;
        width: 100px;
        text-align: right;
    }
}

.team-leave-filters {
    max-width: 1000px;

    @media screen and (min-width: 1150px) {
        max-width: 1260px;
    }

    @media screen and (min-width: 1550px) {
        max-width: 1550px;
    }
}

.team-leave-checkboxes {
    margin-left: 20px;

    @media screen and (min-width: 1150px) {
        margin-left: 0px;
    }

    @media screen and (min-width: 1550px) {
        margin-left: 45px;
    }
}

.create-time-total {
    display: flex;
    justify-content: space-between;
}

.card-end-spacer {
    height: 16px;
    width: 100%;
}
