@side-drawer-width: 360px;

.ss-app-payslip {
	padding-bottom: 60px;

	@media (min-width: @screen-xl) {
		> div {
			float: left;
		}

		&:after {
			content: " ";
			display: table;
			width: 100%;
		}
	}
}

.ss-app-pay-wigets {
	> div {
		margin-bottom: 24px;
	}
	@media (min-width: @screen-xxl) {
		width: 375px;
		float: right !important;
	}

	@media screen and (min-width: @screen-xl) and (max-width: @screen-xl-max) {
		width: 330px;
		float: right !important;
	}
	@media screen and (min-width: @screen-xl) {
		margin: 0 -12px;
	}

	@media screen and (min-width: @screen-md) and (max-width: @screen-lg-max) {
		display: flex;
		flex-wrap: wrap;
		align-items: stretch;
		margin: 0 -12px;
	}
}

.ss-app-graph-wrap {
	@media screen and (min-width: @screen-md) {
		display: flex;
		flex-wrap: wrap;
		align-items: stretch;
	}

	.ss-app-graph {
		position: relative;
		width: 60%;
		max-width: 240px;
		min-width: 125px;

		> div {
			position: absolute;
			top: 0;
			left: 0;
		}

		&::after {
			content: " ";
			display: block;
			padding-top: 100%;
		}

		@media screen and (min-width: @screen-lg-min) {
			flex-wrap: nowrap;
			min-width: 125px;
			flex-grow: 1;
		}

		@media screen and (max-width: @screen-md) {
			max-width: 180px;

			svg text {
				font-size: 22px !important;
			}
		}

		@media screen and (max-width: @screen-sm-max) {
			margin: 0 auto 24px;

			svg text {
				font-size: 20px !important;
			}
		}
	}

	.ss-app-graph-info {
		@media screen and (min-width: @screen-md) {
			width: 60%;
			min-width: 125px;
			flex-grow: 3;
		}

		@media screen and (max-width: @screen-sm-max) {
			padding-left: 0;
		}
	}
}

.ss-app-pie-graph {
	position: relative;
	width: 60%;
	max-width: 240px;
	min-width: 125px;

	> div {
		position: absolute;
		top: 0;
		left: 0;
	}

	&::after {
		content: " ";
		display: block;
		padding-top: 100%;
	}
	@media screen and (max-width: @screen-sm-max) {
		margin-bottom: 24px;
	}
}

.ss-app-breakdown-legend {
	width: 50%;
	padding-left: 12px;
	flex-direction: column;
}

.ss-app-ebreakdown-item {
	position: relative;
	padding-left: 14px;
	line-height: 1.25em;
	font-size: 10px;
	margin-bottom: 4px;

	.item-bullet {
		position: absolute;
		width: 8px;
		height: 8px;
		border-radius: 50%;
		left: 0;
		top: 2px;
	}

	@media screen and (max-width: @screen-md) {
		font-size: 12px;
		margin-bottom: 8px;
		.item-bullet {
			top: 4px;
		}
	}
}

.ss-app-pie-tooltip {
	position: relative;
	padding: 20px 16px 24px;
	border-radius: 2px;
	min-width: 180px;
	color: @white1;
	box-shadow: 5px 10px 15px rgba(114, 114, 114, 0.25);

	.tooltip-arrow {
		width: 0;
		height: 0;
		border-left: 8px solid transparent;
		border-right: 8px solid transparent;
		border-top-width: 8px;
		border-top-style: solid;
		position: absolute;
		bottom: -6px;
		left: 50%;
		margin-left: -2px;
	}
}

.ss-app-history-graph {
	position: relative;

	> div {
		position: absolute;
		top: 0;
		left: 0;
	}

	&::after {
		content: " ";
		display: block;
		padding-top: 85%;
	}
}

.ss-app-pay-history-tooltip {
	padding: 12px;
	border-radius: 5px;
	color: @white1;
	background-color: #1268b3;
}

.ss-app-pay-summary-group {
	padding: 12px 24px;
	margin: 0 -12px;
	border-bottom: 1px solid @gray3;

	&:last-child {
		border-bottom: 0;
	}
}

.ss-app-pay-summary-item {
	padding: 4px 0;
	> label {
		font-weight: bold;
		width: 150px;
	}
	> span {
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		width: ~"calc(100% - 150px)";
	}
}

.ss-app-item-detail {
	position: relative;
	padding: 12px 36px;
	border-radius: 10px;
	width: 100%;
	background-color: @light-gray2;

	&.ss-app-item-detail-heading {
		background-color: transparent;
	}

	.label {
		flex-grow: 1;
		width: calc(100% - 275px);
		min-width: 120px;
		max-width: 320px;
	}
	> span {
		flex-grow: 2;
	}

	span.item-date,
	span.item-unit,
	span.item-rate {
		font-size: 12px;
	}

	span.item-date {
		width: 75px;
	}

	span.item-unit {
		width: 40px;
	}

	span.item-rate {
		width: 60px;
	}

	span.item-amount {
		width: 100px;
	}

	@media screen and (max-width: @screen-sm-max) {
		padding: 12px 36px 12px 28px;
	}
}

.ss-app-earnings {
	padding: 16px 0;
}

.ss-app-earnings-details {
	padding-left: 40px;
}

.ss-app-earnings-details-col-head {
	display: flex;
}

.ss-app-pay-insight-content {
	padding: 16px 24px;
	border-radius: 10px;
	margin-bottom: 32px;
}

.ss-app-pay-insight-item {
	position: relative;
	padding: 4px 16px 4px 36px;
	> svg {
		position: absolute;
		left: 0;
		top: 6px;
	}
}

.ss-app-payslip-list {
	width: @side-drawer-width;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	padding: 20px 16px;
	transition: all ease-out 200ms;
	transform: translateX(@side-drawer-width);
	z-index: 13;

	body.ss-dashboard-wrapper & {
		top: @main-header-height;
	}

	&.show {
		transform: translateX(0);
	}
	@media screen and (max-width: @screen-sm-max) {
		top: 0 !important;
		width: 100%;
		transform: translateX(100%);
		z-index: 99;
	}
}

.ss-app-payslip-list-heading {
	padding-right: 32px;
	margin-bottom: 20px;

	.ss-app-payslip-list-close {
		position: absolute;
		top: 50%;
		right: 0;
		margin-top: -20px;
	}
}

.ss-app-payslip-item {
	position: relative;
	padding: 8px 28px 8px 12px;
	border-radius: 10px;
	border: 2px solid @light-gray2;
	background-color: @light-gray2;
	cursor: pointer;
	.user-select-off();

	> span {
		flex-wrap: nowrap;
	}

	.payslip-details {
		flex-grow: 3;
		text-align: left;
	}

	&:hover {
		border-color: fade(@gray4, 30%);
		background-color: fade(@gray4, 30%);
		color: inherit;
	}

	.btn-file-download {
		position: absolute;
		right: 10px;
		top: 20px;
		z-index: 100;
	}

	&:active {
		border-color: @blue;
		&::after {
			content: " ";
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			z-index: 99;
			background-color: fade(#0c96e4, 20%);
		}
	}
}

.ss-app-payslip-list-overlay {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	opacity: 0;
	z-index: 12;

	&.show {
		right: 0;
	}
}

.ss-app-payslip-list-previous {
	max-height: calc(100vh - 265px);
	margin-right: -16px;
	padding-right: 8px;
	overflow-y: auto;

	@media screen and (max-width: @screen-sm-max) {
		max-height: calc(100vh - 268px);
	}

	body.ss-dashboard-wrapper & {
		max-height: calc(100vh - 281px);
	}
}

.ss-app-payslip-pagination-prev,
.ss-app-payslip-pagination-next {
	background: linear-gradient(348.86deg, #f0f4fd -56.86%, #ffffff 126.39%);
	border-radius: 10px !important;
	padding-top: 1px !important;
	svg {
		margin-top: 2px;
	}
}

.ss-app-payslip-pagination-input {
	border: 1px solid @border-color-base;
	display: inline-block;
	min-width: 110px;
	border-radius: 10px;
	color: @gray6;
	text-align: center;
	padding: 8px 16px;
	line-height: 18px;
	height: 36px;
	margin: 0 8px;
	@media screen and (max-width: @screen-sm-max) {
		padding: 6px 12px;
		font-size: 14px;
		height: 32px;
	}
}

.ss-app-payslip-pagination-prev svg {
	transform: rotate(90deg);
	margin-right: 2px !important;
}

.ss-app-payslip-pagination-next svg {
	transform: rotate(-90deg);
	margin-left: 2px !important;
}

.ss-app-pay-detail-pagination {
	position: fixed;
	bottom: 0;
	left: 32px;
	right: 32px;
	padding: 16px 0;

	@media screen and (min-width: @screen-xxl) {
		left: 40;
		right: @side-drawer-width + 40 + 48;
	}

	@media screen and (min-width: @screen-xl) and (max-width: @screen-xl-max) {
		width: ~"calc(100% - 434px)";
		padding-right: 32px;
	}

	@media screen and (min-width: @screen-md) and (max-width: @screen-lg-max) {
		left: 24px;
		right: 24px;
	}

	@media screen and (min-width: @screen-md-max) {
		body.ss-dashboard-wrapper & {
			left: @main-nav-width + 40;
		}
	}

	@media screen and (max-width: @screen-sm-max) {
		display: none;
		// Hide element since pagination is not yet implemented
		// left: 0;
		// right: 0;
		// padding: 12px;

		> div:last-child {
			display: none;
		}
	}
}

@media screen and (min-width: @screen-md) and (max-width: @screen-lg-max) {
	.ss-app-overview-wrapper {
		display: flex;
		justify-content: stretch;
	}

	.ss-app-earning-breakdown,
	.ss-app-payslip-history {
		width: 50%;
		padding: 0 12px;
		.ss-app-breakdown-wrap {
			height: 260px;
		}

		.ss-app-pay-summary {
			width: 100%;
		}

		.ss-app-history-graph::after {
			padding-top: 280px;
		}
	}
	.ss-app-pay-summary {
		width: 100%;
		margin-left: 12px;
		margin-right: 12px;
	}

	.ss-app-pay-summary-group {
		margin: 0 auto;
	}

	.ss-app-pay-summary-wrap {
		padding: 8px 0;
		> div {
			width: 50%;
			float: left;

			&:first-child {
				border-bottom: 0;
				border-right: 1px solid @gray3;
			}
		}
		&:after {
			content: " ";
			display: table;
			width: 100%;
		}
	}
}

.ss-app-detail-empty {
	text-align: center;
	padding: 40px;
	border-radius: 10px;
	border: 1px solid @gray4;
}
